@import 'reset';
@import 'mixin';
@import 'grid';

$mainColor: #7828B4;
$titleColor: #9013FE;
$conditionColor: #EBEBEB;
$backgroundColor: #f2f2f2;
$tagColor: #D7D7D7;
$dateAndCityColor: #9b9b9b;

body {
    font-family: 'Roboto', sans-serif;
    background-color: $backgroundColor;
    .container-fluid {
        header {
            height: 92px;
            background-color: $mainColor;
            a {
                @include hide-text;
                background-image: url(../Images/logo.svg);
                background-repeat: no-repeat;
                background-position-y: center;
                background-position-x: 20%; }
            .seachBar {
                width: 388px;
                padding-bottom: 5px;
                border-bottom: 1px solid white;
                position: relative;
                i {
                    position: absolute;
                    left: 5px;
                    top: -1px;
                    font-size: 22px;
                    color: white;
                    line-height: 32px; }
                .seach {
                    width: 388px;
                    height: 32px;
                    font-size: 20px;
                    background-color: $mainColor;
                    border: none;
                    padding-left: 32px;
                    color: #fff;
                    &::placeholder {
                        font-family: "Roboto-Italic";
                        font-size: 20px;
                        color: rgba(255,255,255,0.50);
                        font-style: italic; }
                    &:focus {
                        outline: none; } } } }
        main {
            position: relative;
            & > * {
                display: inline-block; }
            .conditionBlock {
                width: 300px;
                margin: 0 38px;
                & > * {
                    background-color: $conditionColor;
                    border-bottom: 1px solid $tagColor;
                    h2 {
                        font-size: 20px;
                        font-family: "Roboto-Bold";
                        font-weight: bold;
                        padding: 24px 0 0 40px; } }
                .location {
                    height: 127px;
                    select {
                        background-color: #fff;
                        margin: 15px 0 0 40px;
                        width: 220px;
                        height: 40px; } }
                .date {
                    height: 187px;
                    .startDate,.endDate {
                        margin-top: 24px;
                        label {
                            padding-right: 13px;
                            padding-left: 44px; }
                        input {
                            width: 169px;
                            height: 40px; } }
                    .endDate label {
                        padding-left: 63px; } }
                .categories {
                    height: 225px;
                    h2 {
                        margin-bottom: 24px; }
                    .form-check {
                        margin: 8px 0;
                        padding-left: 60px;
                        label {
                            font-size: 16px; } }
                    .form-check-input {
                        position: absolute;
                        top: -5px; } } }
            .showResult {
                .seachResult {
                    h2 {
                        font-size: 24px;
                        margin-top: 24px;
                        .dataLen {
                            font-family: Roboto-Bold;
                            color: $titleColor;
                            font-weight: bold; } }
                    .tag {
                        display: inline-block;
                        padding: 8px 38px 8px 16px;
                        border: 1px solid  $titleColor;
                        border-radius: 100px;
                        position: relative;
                        color: $titleColor;
                        margin-top: 8px;
                        margin-right: 8px;
                        i {
                            position: absolute;
                            right: 15px;
                            top: 9px; } }
                    ul {
                        li {
                            margin: 24px 0px;
                            .locationImg {
                                background-image: url(../Images/a.jpg);
                                background-position: center;
                                background-repeat: no-repeat;
                                background-size: cover; }
                            .b {
                                background-image: url(../Images/b.jpg); }
                            .c {
                                background-image: url(../Images/c.jpg); }
                            .infoBlock {
                                padding: 24px 0 0 20px;
                                background-color: #fff;
                                h2 {
                                    font-size: 24px;
                                    color: $titleColor;
                                    font-weight: bold;
                                    margin-top: 0;
                                    margin-bottom: 16px;
                                    width: 70%;
                                    line-height: 36px; }
                                p {
                                    font-size: 16px;
                                    line-height: 24px;
                                    overflow: hidden;
                                    height: 50px;
                                    text-overflow: ellipsis;
                                    margin-bottom: 13px; }
                                .organizer {
                                    font-size: 16px;
                                    font-weight: bold;
                                    margin: 16px 0; }
                                .liTags {
                                    display: inline-block;
                                    border-radius: 100px;
                                    background-color: $tagColor;
                                    color: #fff;
                                    text-align: center;
                                    padding: 3px 16px;
                                    margin-left: 20px; }
                                .eventContent {
                                    margin-top: 16px;
                                    margin-bottom: 24px;
                                    .locationCity,.date {
                                        display: inline-block;
                                        color: $dateAndCityColor;
                                        i {
                                            font-size: 20px;
                                            color: black;
                                            margin-right: 7px; } }
                                    .locationCity {
                                        margin-right: 20px; } } } } }
                    .pages {
                        position: absolute;
                        right: 9.4%;
                        .active {
                            background: $titleColor;
                            color: #fff;
                            a {
                                color: #fff; } }
                        li {
                            width: 41px;
                            height: 41px;
                            line-height: 41px;
                            text-align: center;
                            background-color: #fff;
                            color: $titleColor;
                            border: 1px solid #ECEEEF;
                            border-bottom: none;
                            border-top: none;
                            transition: .3s all;
                            a {
                                color: $titleColor; } } } } } } } }
